(function() {
    'use strict';

    angular.module('uiShell.common', ['restangular', 'ui.router']);
})();
(function() {
    'use strict';

    angular
        .module('uiShell.common')
        .directive('helloWorldCommon', HelloWorldCommon)

    function HelloWorldCommon() {
        var directive = {
            restrict: 'E',
            template: '<span>Hello World from the Common Area</span>'
        };

        return directive;
    }
})();
(function() {
    'use strict';

    angular.module('uiShell.main', ['restangular', 'ui.router']);
})();


(function() {
   'use strict';

    angular
        .module('uiShell.main')
        .controller('MainController', MainController);

    function MainController(ExampleService) {
        var vm = this;

        vm.message = 'Hello World!!';
        vm.logMessage = function(message) {
            ExampleService.logMessage(message);
        }
    }

    MainController.$inject = ['ExampleService'];
})();


(function() {
    'use strict';

    angular.module('uiShell.main')
        .factory('ExampleService', ExampleService);

    function ExampleService($log) {
        var service = {
            logMessage: logMessage
        }

        return service;

        function logMessage(message) {
            $log.log(message);
        }
    }

    ExampleService.$inject = ['$log'];
})();

(function() {
    'use strict';

    angular
        .module('uiShell.main')
        .directive('helloWorldTitle', HelloWorldTitle)

    function HelloWorldTitle() {
        var directive = {
            restrict: 'E',
            templateUrl: 'app/domain1/partials/example-directive-template.html',
            scope: {
                message: '=message'
            }
        };

        return directive;
    }
})();
(function() {
    angular.module('uiShell', ['restangular', 'ui.router', 'uiShell.main', 'uiShell.common', 'sky']);
})();






(function() {
    'use strict';

    angular
        .module('uiShell')
        .run(runBlock);

    function runBlock() {

    }

})();
(function() {
    'use strict';

    angular
        .module('uiShell')
        .config(routerConfig);

    function routerConfig($stateProvider, $urlRouterProvider) {
        $stateProvider
            .state('home', {
                url: '/',
                templateUrl: 'app/domain1/main.html',
                controller: 'MainController',
                controllerAs: 'main'
            });

        $urlRouterProvider.otherwise('/');
    }

    routerConfig.$inject = ['$stateProvider', '$urlRouterProvider'];
})();
(function() {
    'use strict';

    angular
        .module('uiShell')
        .config(config);

    /** @ngInject */
    function config($logProvider, $locationProvider) {
        // Enable log
        $logProvider.debugEnabled(true);
        $locationProvider.html5Mode({
            enabled: true,
            requireBase: false
        });
    }

    config.$inject = ['$logProvider', '$locationProvider'];

})();

angular.module("uiShell").run(["$templateCache", function($templateCache) {$templateCache.put("app/domain1/main.html","<div>Blah2<br><hello-world-title message=\"main.message\"></hello-world-title>{{main.sizeOfArray}}</div><hello-world-common></hello-world-common><button ng-click=\"main.logMessage(\'blah\')\">Click Me</button>");
$templateCache.put("app/domain1/partials/example-directive-template.html","<p>Hello World</p>");}]);